.products__title {
    font-family: 'Titan One';
    font-size: 58px;
    color: #D41443;
    font-weight: 400;
    line-height: 1.5;
    display: block;
    text-align: center;
    margin-bottom: 188px;
    margin-top: auto;
}

a.products {
    font-family: 'Titan One';
    font-size: 18px;
    font-weight: 400;
    line-height: 1.2;
    color: #FFA5BA;
    display: block;
    text-align: center;
    margin-top: 100px;
}

.product {
    list-style-type: none;
    margin-left: auto;
    margin-right: auto;
    padding: 0;
}

.product__item {
    max-width: 329px;
    height: 485px;
    border: none;
    border-radius: 24px;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 185px;
}

.product__name {
    font-family: 'Titan One';
    color: #FFFFFF;
    font-weight: 400;
    text-align: center;
    position: relative;
    top: -144px;
}

.product__name::after {
    content: '. . .';
    display: block;
    padding-top: 6px;
}

.product__name.icecream::before {
    position: relative;
    content: "";
    width: 100%;
    height: 340px;
    display: block;
    background-image: url(../images/products/product_1_mobile_@1x.png);
    background-size: cover;
    background-position: center;
    margin-bottom: 38px;
}

.product__name.coffee::before {
    position: relative;
    content: "";
    width: 100%;
    height: 340px;
    display: block;
    background-image: url(../images/products/product_2_mobile_@1x.png);
    background-size: cover;
    background-position: center;
    margin-bottom: 38px;
}

.product__name.milksheake::before {
    position: relative;
    content: "";
    width: 100%;
    height: 340px;
    display: block;
    background-image: url(../images/products/product_3_mobile_@1x.png);
    background-size: cover;
    background-position: center;
    margin-bottom: 38px;
}

.product__description {
    font-family: 'DM Sans';
    font-size: 14px;
    font-weight: 700;
    line-height: 1.5;
    color: #FFFFFF;
    text-align: center;
    position: relative;
    top: -150px;
}

.product__image {
    position: relative;
    width: 100%;
    top: -150px;
    left: -37px;
}


.bg__1 {
    background-color: #FFB8CA;
}

.bg__2 {
    background-color: #F0D1A5;
}

.bg__3 {
    background-color: #C2E297;
}

img {
    display: block;
    width: 100%;
}

.button--product {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: white;
  
    fill: #D41443;
    height: 25px;
    width: 25px;
    border: none;
    border-radius: 50%;
    cursor: pointer;
    margin-left: auto;
    margin-right: auto;
    margin-top: -125px;
    }

@media (min-device-pixel-ratio: 2),
    (-webkit-min-device-pixel-ratio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
    .product__name.icecream::before {
        background-image: url(../images/products/product_1_mobile_@2x.png);
    }
    .product__name.coffee::before {
        background-image: url(../images/products/product_2_mobile_@2x.png);
    }
    .product__name.milksheake::before {
        background-image: url(../images/products/product_3_mobile_@2x.png);
    }
    }

@media screen and (min-width: 768px) {
    .product {
        display: flex;
        gap: 20px;
    }
    .bg__1 {
        margin-left: 10px;
    }
    .bg__3 {
        margin-right: 10px;
    }
    .product__name.icecream::before {
        background-image: url(../images/products/product_1_tablet_@1x.png);
    }
    .product__name.coffee::before {
        background-image: url(../images/products/product_2_tablet_@1x.png);
    }
    .product__name.milksheake::before {
        background-image: url(../images/products/product_3_tablet_@1x.png);
    }
}

@media(min-width: 768px) and (min-device-pixel-ratio: 2),
(min-width: 768px) and (-webkit-min-device-pixel-ratio: 2),
(min-width: 768px) and (min-resolution: 192dpi),
(min-width: 768px) and (min-resolution: 2dppx) {
    .product__name.icecream::before {
        background-image: url(../images/products/product_1_tablet_@2x.png);
    }
    .product__name.coffee::before {
        background-image: url(../images/products/product_2_tablet_@2x.png);
    }
    .product__name.milksheake::before {
        background-image: url(../images/products/product_3_tablet_@2x.png);
    }
}

@media(min-width: 1280px) {
    .product__name.icecream::before {
        background-image: url(../images/products/product_1_desktop_@1x.png);
    }
    .product__name.coffee::before {
        background-image: url(../images/products/product_2_desktop_@1x.png);
    }
    .product__name.milksheake::before {
        background-image: url(../images/products/product_3_desktop_@1x.png);
    }
}

@media(min-width: 1280px) and (min-device-pixel-ratio: 2),
(min-width: 1280px) and (-webkit-min-device-pixel-ratio: 2),
(min-width: 1280px) and (min-resolution: 192dpi),
(min-width: 1280px) and (min-resolution: 2dppx) {
    .product__name.icecream::before {
        background-image: url(../images/products/product_1_desktop_@2x.png);
    }
    .product__name.coffee::before {
        background-image: url(../images/products/product_2_desktop_@2x.png);
    }
    .product__name.milksheake::before {
        background-image: url(../images/products/product_3_desktop_@2x.png);
    }
}